<template>
    <div class="rating-container">
        <span v-for="star in stars">
            <i class="fa" v-bind:class="star" aria-hidden="true"></i>
        </span>
    </div>
</template>

<script>
    export default {
        props:{
            rating: Number, //   
            maxStars: Number //  
        },
/*        data(){
            return{
                stars:[], //  
                cR: 0
            }
        },*/
        computed:{
            cR: function(){
                let lcR=0;
                if (!(this.rating)){ //   -     0%    -  0%
                    lcR = 0;
                }else if (this.rating > 100){ //   -     100% -  100%
                    lcR = 100;
                } else{
                    lcR = this.rating;
                }
                return lcR; 
            },
            stars: function(){ //  
                let st = [];                
                let pS = 100/(this.maxStars*2); //   
               // console.log('cR: ' + this.cR);
               // console.log('pS: ' + pS);
                let xz=0; //      
                for (let i = 1; i <= this.maxStars; i++){
                    xz = (this.cR-(i-1)*2*pS)/((i*2*pS)-(i-1)*2*pS);
                  //  console.log('xz: ' + xz);
                    if (xz >= 0.67) { 
                        st.push('fa-star'); //  
                    } else if (xz <= 0.33) {
                        st.push('fa-star off-star');                        
                    } else { //  
                        st.push('fa-star-half');
                    }

                }
               // console.log(st);
                return st;
            }
        }
    }
</script>

<style scoped>
    .rating-container{
        display: block;        
        margin: 15px 0;
    }
    .fa-star{
        color:gold;
    }
    .fa-star.off-star{
        color: #eee;
    }
    
    .fa-star-half,
    .fa-star-half-o { /* fa-star-half */
        color: gold;
    }    
</style>


