<template>    
    <div class="filter-container">
        <p class="catHeader">:</p>
        <label class="mv-cat-checkbox" v-for="(key, value, index) in categories">
            <input type="checkbox" v-bind:id="'mv-mv-cat-checkbox-' + index" v-model="checkedCategories" v-bind:value="value" @change="filterCatDone"><span>{{ value?value:'' }} <i class="mv-qty">{{key}}</i></span>
        </label>
        <p class="typeHeader"> :</p>
        <label class="mv-type-checkbox" v-for="(key,value,index) in bookTypes">
            <input type="checkbox" v-bind:id="'mv-mv-type-checkbox-' + index" v-model="checkedTypes" v-bind:value="value" @change="filterTypesDone"><span>{{ value?value:'' }} <i class="mv-qty">{{key}}</i></span>
        </label>
    </div>
</template>

<script>
    export default {
        props:{
            filteredBooks: Array, /*    */
        },    
        data() {
            return {
                checkedCategories: [],
                checkedTypes: [] 
            }
        },
        methods: {            
            unic: function(arr, key){ /*         -   */
                let result = {}; 
                /*      -   
                    { 
                        ' ': 2,
                        '': 3,
                        ...
                      } 
                */
                arr.forEach(function(a){ /*  a = book[0..n] */
                    /*  book[0].GENRE = [" ", ""]      result */
                    a[key].forEach(function(b){ /*  b = book[0].GENRE[0] = " " */
                        result[b.toLowerCase()] = result[b.toLowerCase()] + 1 || 1;  /*  { " " : 2} */
                    });
                });
                return result;
            },
            unic_type: function(arr, key){ /*           -   */
                let result = {}; 
                /*      -   
                    { 
                        '': 2,
                        '': 3,
                        ...
                      } 
                */
                arr.forEach(function(a){ /*  a = book[0..n] */
                    /*  book[0].TYPE = {
                            "1099": "",
                            "1100": "
                            }      result */
                    for (let b in a[key]){ /*  b = "1099","1100"  .. -  */
                        result[a[key][b].toLowerCase()] = result[a[key][b].toLowerCase()] + 1 || 1;  /*  { "" : 2} */
                    };
                });
                return result;
            },          
            filterCatDone: function() {
                //   'filter-cat-done'     
               // console.log('checkedCategories'); console.log(this.checkedCategories);
                this.$emit('filter-cat-done', this.checkedCategories);
            },
            filterTypesDone: function() {
                //   'filter-types-done'     
               // console.log('checkedTypes'); console.log(this.checkedTypes);
                this.$emit('filter-types-done', this.checkedTypes);
            }            
        },
        computed: {
            categories: function () {
                //     categories[ {label: qty} ]  :  }
                //      ,      ..     -
                return this.unic(this.filteredBooks, 'GENRE');
            },
            bookTypes(){
                //       bookTypes[ {label: qty} ]  :  }
                return this.unic_type(this.filteredBooks, 'TYPE');
            }
        }
    }
</script>
<style scoped>
 /*
 *   
 */
    label.mv-cat-checkbox span,
    label.mv-type-checkbox span{
        padding: 5px 20px;
        border-radius: 50px;
        margin: 10px 2px;
        display: inline-block;
        cursor: pointer;
    }
    
    /*
    *    
    */
    label.mv-cat-checkbox span{
        background-color: #fff;
        color: #17ad8f;
        /* border: 1px dashed #03bd98; */
        border: 2px solid #03bd98;
/*        background-color: #ccc;
        border: none;
        color: #fff;*/

    }
    .mv-cat-checkbox input:checked + span { /*   */
/*        background-color: #ae6ab0;*/
        background-color: #03bd98;
        color: white;
    }
    
    /*
    *    
    */
    label.mv-type-checkbox span{
        background-color: #fff;
        color: #ae6ab0;;
/*        border: 1px dashed #ae6ab0;     */
        border: 2px solid #ae6ab0;  
    }
    .mv-type-checkbox input:checked + span { /*   */
        background-color: #ae6ab0;
        color: white;
    }
    
    i.mv-qty{
        padding: 3px;
        border: none;
        border-radius: 50px;
        font-size: 10px;
        font-style: normal;
        vertical-align: middle;
/*      background-color: gold;
        color: #2c3e50;            */
    }
    
    /* /  */
    
    /*   */
    .mv-cat-checkbox input,
    .mv-type-checkbox input {
        display: none;
    }     
    
    *{
        box-sizing: border-box;
    }
    .filter-container{
        width: 100%;
        margin: 45px auto;        
    }
    *:active,
    *:focus {
        outline: none;
        box-shadow: none;
    }
    /*
    *   :   
    */
    .catHeader,
    .typeHeader{
        color: #797979;
        font-size: 18px;
        font-weight: 100;
        margin: 10px 0 0;
    }


    /*
    * 
    */    
    span.filter-open{
        position: absolute;
        display: inline-block;
        background-color: whitesmoke;
        right: 15px;
        top: 0;
        bottom: 0; 
        padding: 0px 5px;
        border: none;
        border-radius: 50px;
        height: 45px;
        width: 45px;
        line-height: 50px;
        cursor: pointer;

    }
    .filter-open svg{
        color: #ccc;
        width: 19px;
        height: 45px;
    }
</style>


